body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background-color: #fbfbfb;
}

.wrapper {
    width: 500px;
    margin: 10px auto;
    font-size: 14px;
    background-color: #fff;
    border: 1px solid #f1f1f1;
    padding: 20px;
}

/* 评论框样式 */
.comment-input {
    background-color: #fff;
    border: 1px solid #f1f1f1;
    padding: 20px;
    margin-bottom: 10px;
}

.comment-field {
    margin-bottom: 15px;
    display: -ms-flexbox;
    display: flex;
}

.comment-field .comment-field-name {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-preferred-size: 100px;
        flex-basis: 100px;
    font-size: 14px;
}

.comment-field .comment-field-input {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
}

.comment-field-input input,
.comment-field-input textarea {
    border: 1px solid #e6e6e6;
    border-radius: 3px;
    padding: 5px;
    outline: none;
    font-size: 14px;
    resize: none;
    -ms-flex: 1 1;
        flex: 1 1;
}

.comment-field-input textarea {
    height: 100px;
}

.comment-field-button {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.comment-field-button button {
    padding: 5px 10px;
    width: 80px;
    border: none;
    border-radius: 3px;
    background-color: #00a3cf;
    color: #fff;
    outline: none;
    cursor: pointer;
}

.comment-field-button button:active {
    background: #13c1f1;
}

/* 评论列表样式 */
.comment-list {
    background-color: #fff;
    border: 1px solid #f1f1f1;
    padding: 20px;
}

/* 评论组件样式 */
.comment {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    border-bottom: 1px solid #f1f1f1;
    margin-bottom: 10px;
    padding-bottom: 10px;
    min-height: 50px;
}

.comment .comment-user {
    -ms-flex-negative: 0;
        flex-shrink: 0;
}

.comment-username {
    color: #00a3cf;
    font-style: italic;
}

.comment-createdtime {
    padding-right: 5px;
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 5px;
    font-size: 12px;
}

.comment:hover .comment-delete {
    color: #00a3cf;
}

.comment-delete {
    position: absolute;
    right: 0;
    top: 0;
    color: transparent;
    font-size: 12px;
    cursor: pointer;
}

.comment p {
    margin: 0;
    /*text-indent: 2em;*/
}

code {
    border: 1px solid #ccc;
    background: #f9f9f9;
    padding: 0px 2px;
}


